import React, { FormEvent, useRef, useState } from 'react';
import styles from './reset-password.module.css';
import { Link, Redirect } from 'react-router-dom';
import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { useDispatch, useSelector } from 'react-redux';
import { resetPasswordFetch } from '../../services/actions/profile-actions';
import { RootState } from '../../utils/types';

const ResetPassword: React.FC = () => {

  const dispatch = useDispatch();

  const [password, setPassword] = useState<string>('')
  const [token, setToken] = useState<string>('')

  const inputRef = useRef(null)

  const [passwordType, setPasswordType] = useState<"password" | "text">('password')
  const onIconClick = () => {
      if(passwordType === 'password') {
          setPasswordType('text')
      } else {
          setPasswordType('password')
      }
  }

  const resetHandler = (e: FormEvent) => {
      e.preventDefault();
      dispatch(resetPasswordFetch(password, token));
  } 

  const name = useSelector<RootState, string>(store => store.profileReducer.name);
  const passwordReseted = useSelector<RootState, boolean>(store => store.profileReducer.passwordReseted);
  const emailSent = useSelector<RootState, boolean>(store => store.profileReducer.emailSent);
  if (name) {
      return (
        <Redirect
          to={{
            pathname: '/'
          }}
        />
      );
  }
  if (!emailSent) {
      return (
        <Redirect
          to={{
            pathname: '/forgot-password'
          }}
        />
      );
  }
  if (passwordReseted) {
      return (
        <Redirect
          to={{
            pathname: '/login'
          }}
        />
      );
  }
    
  return (
    <div className={styles.container}>
        <p className="text text_type_main-medium mt-20">
            
        </p>
        <form 
            className={`${styles.form} mt-6`}
            onSubmit={resetHandler}
        >
            <Input
                type={passwordType}
                placeholder={'  '}
                onChange={e => setPassword(e.target.value)}
                icon={'ShowIcon'}
                value={password}
                name={'password'}
                error={false}
                ref={inputRef}
                onIconClick={onIconClick}
                errorText={''}
                size={'default'}
            />
            <Input
                type={'text'}
                placeholder={'   '}
                onChange={e => setToken(e.target.value)}
                icon={undefined}
                value={token}
                name={'token'}
                error={false}
                ref={inputRef}
                onIconClick={onIconClick}
                errorText={''}
                size={'default'}
            />
            <div className={`${styles.button} mt-6`}>
                <Button type="primary" size="medium">
                    
                </Button>
            </div>
        </form>
        <div className={`${styles.info} mt-20`}>
            <p className="text text_type_main-default text_color_inactive pt-4">
                 ?
            </p>
            <Link to='/login'>
                <Button type="secondary" size="medium">
                    
                </Button>
            </Link>
        </div>
    </div>

  );
}

export default ResetPassword;
